// BREAKPOINT
$phone: 576px;
$tablet: 768px;
$desktop: 992px;
$lg-desktop: 1200px;

@mixin phone {
  @media (min-width: 320px) and (max-width: #{$tablet}) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: #{$tablet}) and (max-width: #{$desktop - 1px}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: #{$desktop}) and (max-width: #{$lg-desktop}) {
    @content;
  }
}

@mixin lg-desktop {
  @media (min-width: #{$lg-desktop}) {
    @content;
  }
}
